import React, { useState } from 'react';

const Sidebar = ({ onMenuSelect }) => {
  const [state, setState] = useState(0); // 0: 아이콘만, 1: 펼침(내용 없음), 2: 전체 표시

  const toggleSidebarState = () => {
    setState((prevState) => (prevState + 1) % 3); // 0 -> 1 -> 2 -> 0 순환
  };

  return (
    <aside className={`flex-shrink-0 ${state === 0 ? 'w-24' : state === 1 ? 'border w-64 h-[96px] border-[#737373] ':state === 2 ? 'h-[100vh] w-64 border-r border-[#737373]': ''} `}>
      <div className="flex flex-col h-full">
        {/* 사이드바의 상단 영역 */}
        <div>
          <a
            href="#"
            onClick={(e) => {
              e.preventDefault();
              toggleSidebarState(); // 사이드바 상태 토글 함수
            }}
            className={`flex items-center p-2 text-gray-500 transition-colors rounded-md dark:text-light hover:bg-primary-100 dark:hover:bg-primary ${state > 0 ? 'bg-primary-100 dark:bg-primary' : ''}`}
            role="button"
            aria-haspopup="true"
            aria-expanded={state > 0 ? 'true' : 'false'}
            >
    <span aria-hidden="true">
    <svg
      className={`${state === 0 ? 'hidden' : state === 1 ? 'translate-y-5 w-[40px] h-[40px]':state===2 ? 'translate-y-5 w-[40px] h-[40px]':''}`}
      xmlns="http://www.w3.org/2000/svg"
      fill="none"
      viewBox="0 0 24 24"
      stroke="currentColor"
    >
      <path
        strokeLinecap="round"
        strokeLinejoin="round"
        strokeWidth="2"
        d="M3 12l2-2m0 0l7-7 7 7M5 10v10a1 1 0 001 1h3m10-11l2 2m-2-2v10a1 1 0 01-1 1h-3m-6 0a1 1 0 001-1v-4a1 1 0 011-1h2a1 1 0 011 1v4a1 1 0 001 1m-6 0h6"
      />
    </svg>
    </span>
    <span className={`${state === 0 ? 'hidden' : state === 1 ? 'absolute translate-y-5 translate-x-1 text-xl font-bold left-[60px]':state===2 ? 'absolute translate-y-5 translate-x-1 text-xl font-bold left-[60px]':''}`}> 메뉴 </span>
    <span className="ml-auto" aria-hidden="true">
    <svg
        className={`${
          state === 0
            ? 'rotate-[360deg] absolute top-0 left-0 border w-[96px] h-[96px] border-[#737373]'
            : state === 1
            ? 'rotate-[270deg] absolute top-0 left-[190px] w-[50px] h-[96px] '
            : state === 2
            ? 'rotate-[360deg] absolute top-0 left-[190px] w-[50px] h-[96px] '
            : ''
        }`}
        fill="none"
        viewBox="0 0 24 24"
        stroke="currentColor"
      >
        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M19 9l-7 7-7-7" />
      </svg>
    </span>
  </a>
</div>

        {state > 0 && (
          <nav aria-label="Main" className="flex-1 px-4 py-9 space-y-2 overflow-y-hidden hover:overflow-y-auto">
            {state === 2 && (
              <div role="menu" className="mt-2 space-y-2 px-7" aria-label="Dashboards">
                <button
                  onClick={() => onMenuSelect('Main')}
                  className="block w-full text-left p-5 text-sm text-gray-700 transition-colors duration-200 rounded-md dark:text-light hover:text-gray-700"
                >
                  메인
                </button>
                <button
                  onClick={() => onMenuSelect('VideoStream')}
                  className="block w-full text-left p-5 text-sm text-gray-400 transition-colors duration-200 rounded-md dark:hover:text-light hover:text-gray-700"
                >
                  동영상 스트리밍
                </button>
              </div>
            )}
          </nav>
        )}
      </div>
    </aside>
  );
};

export default Sidebar;