import React, { useState, useEffect } from 'react';
import Navbar from './components/Navbar';
import Sidebar from './components/Sidebar';
import Explorer from './components/explorer/Explorer';
import VideoStream from './components/explorer/VideoStream';
import { useNavigate } from 'react-router-dom';

function Home({ onLogout }) {
  const [selectedMenu, setSelectedMenu] = useState('Explorer'); // 기본 메뉴: Explorer
  const [searchTerm, setSearchTerm] = useState('');
  const [uploadedFileName, setUploadedFileName] = useState(''); // 파일 이름 상태
  const navigate = useNavigate();
  const [fileNames, setFileNames] = useState([]); // 파일 이름 배열 상태

  // 파일 이름을 처리하는 함수
  const handleFileNamesSelected = (event) => {
    const files = event.detail;
    console.log('파일 목록:', files); // 디버깅
    setFileNames(files); // 파일 목록 상태 업데이트
  };

  useEffect(() => {
    // 이벤트 리스너 등록
    window.addEventListener('fileNamesSelected', handleFileNamesSelected);

    // 컴포넌트 언마운트 시 이벤트 리스너 제거
    return () => {
      window.removeEventListener('fileNamesSelected', handleFileNamesSelected);
    };
  }, []);

  const handleLogout = () => {
    onLogout();
    navigate('/');
  };

  const handleSearch = (term) => {
    setSearchTerm(term);
  };

  const renderContent = () => {
    const latestFile = fileNames[fileNames.length - 1]; // 배열의 마지막 파일 선택

    switch (selectedMenu) {
      case 'Main':
        return (
          <div className="flex">
            <Explorer
              searchTerm={searchTerm}
              onFileSelected={(fileName) => setUploadedFileName(fileName)}
              className="w-full"
            />
            <div className="flex-1" style={{ width: 500, height: '100%', border: '1px solid #ccc', padding: 20, marginTop: 16 }}>
              {latestFile ? (
                <>
                  <img
                    src={`https://raw.githubusercontent.com/gf678/test_storage/main/${latestFile}`}
                    alt={latestFile}
                    style={{ width: '100%', height: 'auto' }}
                  />
                </>
              ) : (
                <p>미리보기 화면입니다.</p>
              )}
            </div>
          </div>
        );
      case 'VideoStream':
        return <VideoStream />;
      default:
        return (
          <div className="flex">
            <Explorer
              searchTerm={searchTerm}
              onFileSelected={(fileName) => setUploadedFileName(fileName)}
              className="w-full"
            />
            <div className="flex-1" style={{ width: 500, height: '100%', border: '1px solid #ccc', padding: 20, marginTop: 16 }}>
              {latestFile ? (
                <>
                  <img
                    src={`https://raw.githubusercontent.com/gf678/test_storage/main/${latestFile}`}
                    alt={latestFile}
                    style={{ width: '100%', height: 'auto' }}
                  />
                </>
              ) : (
                <p>미리보기 화면입니다.</p>
              )}
            </div>
          </div>
        );
    }
  };
  

  return (
    <div className="flex">
      <Sidebar onMenuSelect={setSelectedMenu} /> {/* Sidebar */}
      <div className="flex-1 flex flex-col">
        <Navbar
          onLogout={handleLogout}
          onSearch={handleSearch}
          uploadedFileName={uploadedFileName}
        /> {/* Navbar */}
        <div className="flex flex-1">{renderContent()}</div>
      </div>
    </div>
  );
}

export default Home;
