import { defineFileAction, ChonkyIconName } from "chonky";

const uploadFileAction = defineFileAction({
    id: "upload",
    button: {
      name: "Upload",
      toolbar: true,
      contextMenu: false, 
      icon: ChonkyIconName.upload
    }
  });
  const downloadFileAction = defineFileAction({
    id: "download",
    button: {
      name: "Download",
      toolbar: false,
      contextMenu: true, 
      icon: ChonkyIconName.download
    }
  });
  const viewFileAction = defineFileAction({
    id: "view",
    requiresSelection: true,
    fileFilter: (file) => file && !file.isDir,
    button: {
      name: "View",
      toolbar: false,
      contextMenu: true,
      icon: ChonkyIconName.file
    }
  });

  export const customActions=[
    uploadFileAction,downloadFileAction,viewFileAction
  ]