import React, { useState, useEffect } from 'react';
import { BrowserRouter as Router, Route, Routes, Navigate } from 'react-router-dom';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import Login from './set/Login';
import Home from './set/Home';

function App() {
  const [isAuthenticated, setIsAuthenticated] = useState(false); // 인증 상태

  // 컴포넌트가 마운트될 때 세션 스토리지에서 상태를 확인
  useEffect(() => {
    const storedUsername = sessionStorage.getItem('username');
    if (storedUsername) {
      setIsAuthenticated(true); // 이미 로그인 상태인 경우
    }
  }, []);

  const login = () => {
    setIsAuthenticated(true); // 로그인 함수
    // 사용자명을 세션 스토리지에 저장
    sessionStorage.setItem('username', '123'); // 예시로 사용, 실제로는 로그인한 사용자 이름으로 변경
  };

  const logout = () => {
    setIsAuthenticated(false); // 로그아웃 함수
    // 세션 스토리지에서 사용자명 삭제
    sessionStorage.removeItem('username');
  };

  return (
    <DndProvider backend={HTML5Backend}> {/* DndProvider로 감싸기 */}
      <Router>
        <Routes>
          <Route 
            path="/" 
            element={!isAuthenticated ? <Login onLogin={login} /> : <Navigate to="/Home" />} 
          />
          <Route
            path="/home"
            element={isAuthenticated ? <Home onLogout={logout} /> : <Navigate to="/" />}
          />
        </Routes>
      </Router>
    </DndProvider>
  );
}

export default App;