const VideoStream = () => {
    const videoUrl = "https://raw.githubusercontent.com/gf678/test_storage/main/1.mp4"; // 예시 URL
  
    return (
      <div>
        <video width="1280" controls>
          <source src={videoUrl} type="video/mp4" />
          브라우저가 비디오 태그를 지원하지 않습니다.
        </video>
      </div>
    );
  }
  
  export default VideoStream;