// src/components/Navbar.js
import React, {useState,useEffect} from 'react';

function Navbar({ onLogout , onSearch , uploadedFileName }) { // onLogout props 추가
  const [username, setUser] = useState("-"); // user 상태 변수
  const [role, setRole] = useState("User"); // 권한 상태 변수 추가
  const [profileImage, setProfileImage] = useState(() => {
    return sessionStorage.getItem('profileImage') || 'avatar.png';
  });
  const [isDropdownOpen, setIsDropdownOpen] = useState(false); // 드롭다운 열기 상태 변수
  const [isprofilePopupOpen, setprofileIsPopupOpen] = useState(false); // 팝업 열기 상태 변수
  const [issettingPopupOpen, setIssettingPopupOpen] = useState(false); // 팝업 열기 상태 변수
  const [searchTerm, setSearchTerm] = useState(''); // 검색어 상태 변수 추가
  const [notifications, setNotifications] = useState(0); // 알림 개수 상태
  const [notificationList, setNotificationList] = useState([]); // 알림 메시지 목록 상태
  const [isNoticeDropdownOpen, setIsNoticeDropdownOpen] = useState(false); // 드롭다운 열기 상태

  // 권한 변경 핸들러
  const handleRoleChange = () => {
    const newRole = window.prompt("새 권한을 입력하세요 (Admin/User):");
    if (newRole) {
      if (username === "특정 이름" || "특정 조건") { // 예: IP나 특정 이름을 기반으로 조건 설정
        if (newRole.toLowerCase() === "admin") {
          setRole("Admin");
          alert(`${username}님이 관리자(Admin)로 승격되었습니다.`);
        } else if (newRole.toLowerCase() === "user") {
          setRole("User");
          alert(`${username}님이 사용자(User)로 강등되었습니다.`);
        } else {
          alert("유효한 권한을 입력하세요 (Admin/User).");
        }
      } else {
        alert("권한을 변경할 수 있는 권한이 없습니다.");
      }
    }
  };
  const handleProfileImageChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onload = () => {
        const imageData = reader.result;
        setProfileImage(imageData); // 이미지 상태 업데이트
        sessionStorage.setItem('profileImage', imageData); // 세션 스토리지에 이미지 저장
      };
      reader.readAsDataURL(file);
    }
  };

  const toggleDropdown = () => {
    setIsDropdownOpen(!isDropdownOpen); // 드롭다운 상태 토글
  };

  const handleLogout = () => {
    onLogout(); // 로그아웃 함수 호출
    setIsDropdownOpen(false); // 드롭다운 닫기
  };

  const toggleprofilePopup = () => {
    setprofileIsPopupOpen(!isprofilePopupOpen);
    setIsDropdownOpen(false); // 팝업 상태 토글
  };

  const togglesettingPopup = () => {
    setIssettingPopupOpen(!issettingPopupOpen);
    setIsDropdownOpen(false); // 팝업 상태 토글
  };
  const toggleNoticePopup = () => {
    setIsNoticeDropdownOpen(!isNoticeDropdownOpen);
    setIsDropdownOpen(false); // 팝업 상태 토글
    setNotificationList([]); // 알림 메시지 초기화
  };
  const handleSearchChange = (e) => {
    setSearchTerm(e.target.value); // 검색어 변경
  };

  const handleSearchSubmit = (e) => {
    e.preventDefault();
    onSearch(searchTerm); // 상위 컴포넌트로 검색어 전달
  };
   // 알림 클릭 핸들러
   const handleNotificationsClick = () => {
    setIsNoticeDropdownOpen(!isNoticeDropdownOpen); // 드롭다운 토글
    if (notifications > 0) {
      setNotifications(0); // 알림 읽음 처리
    }
  };
  const addNotification = (message) => {
    setNotificationList((prev) => [...prev, message]); // 알림 메시지 추가
    setNotifications((prev) => prev + 1); // 알림 개수 증가
  };
  
  useEffect(() => {
    if (uploadedFileName) {
      addNotification(`${uploadedFileName} 추가됨`); // 파일이 업데이트 될 때 알림 추가
    }
  }, [uploadedFileName]); // uploadedFileName이 변경될 때마다 실행

  return (
    <div className="bg-white flex justify-between items-center p-5 w-full border border-[#737373] space-x-7" >
      <p className='text-3xl font-bold text-[#3a989c]'>
        {username}님, 환영합니다.
      </p>
      
       {/* 검색 바 */}
       <form onSubmit={handleSearchSubmit} className="hidden md:flex" style={{ marginLeft: 'auto' }}>
        <input
          type="text"
          value={searchTerm}
          onChange={handleSearchChange}
          placeholder="검색..."
          className="border border-gray-300 rounded-l-full p-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
        <button
          type="submit"
          className="border-t border-b border-r border-gray-300 bg-white text-black p-2 rounded-r-full hover:bg-blue-600"
          style={{ borderLeft: 'none' }}
        >
          검색
        </button>
      </form>

      {/* 알림 버튼 */}
      <div className="relative flex flex-col items-end"> {/* flex-col로 세로 배치 */}
        <button
          onClick={handleNotificationsClick}
          className="flex items-center justify-center w-12 h-12 bg-[#8f8f8f] rounded-full hover:bg-[#2a7c7c] transition duration-200"
        >
          <span className="text-white text-xl">🔔</span>
          {notifications > 0 && (
            <span className="absolute top-0 right-0 bg-red-500 text-white rounded-full w-5 h-5 flex items-center justify-center text-xs">
              {notifications}
            </span>
          )}
        </button>
  </div>
      {/* 프로필 버튼 */}
      <div className="relative">
        <button onClick={toggleDropdown}>
          <img
            src={profileImage} // 업데이트된 이미지 경로
            alt="Profile"
            className="w-12 h-12 rounded-full border border-[#737373] cursor-pointer"
          />
        </button>
 

        {/* 드롭다운 메뉴 */}
        {isDropdownOpen && (
          <div className="absolute right-0 mt-2 w-48 bg-white border border-[#737373] shadow-lg rounded z-10 transform -translate-x-2">
            <ul className="py-2">
              <li 
                className="px-4 py-2 hover:bg-[#28fcf5] hover:text-white cursor-pointer"
                onClick={toggleprofilePopup} // 팝업 열기
              >
                내 프로필
              </li>
              <li 
                className="px-4 py-2 hover:bg-[#28fcf5] hover:text-white cursor-pointer"
                onClick={togglesettingPopup} // 팝업 열기
              >
                설정
              </li>
              <li 
                className="px-4 py-2 hover:bg-[#28fcf5] hover:text-white cursor-pointer"
                onClick={handleLogout} // 로그아웃 클릭 핸들러
              >
                로그아웃
              </li>
            </ul>
          </div>
        )}
      </div>

      {/* 팝업 내용 */}
      {isprofilePopupOpen && (
        <div className="fixed top-0 right-0 w-1/3 h-full bg-white shadow-lg border border-[#737373] p-5">
          <h2 className="text-lg font-bold">내 프로필</h2>
          <img
              src={profileImage}
              alt="사진을 삽입해 주세요"
              className="w-40 h-40 border border-[#737373]"
            />
          <p>이름:{username}</p> 
          <p>권한:{role}</p>
          <button onClick={toggleprofilePopup} className="mt-4 bg-red-500 text-white p-2 rounded">닫기</button>
        </div>
      )}
      {/* 팝업 내용 */}
      {issettingPopupOpen && (
        <div className="fixed top-0 right-0 w-1/3 h-full bg-white shadow-lg border border-[#737373] p-5">
          <h2 className="text-lg font-bold">설정</h2>
          <div className="mb-4">
            <label className="block text-gray-700">이름 변경</label>
            <button 
              onClick={() => {
                const newName = window.prompt("새 이름을 입력하세요:");
                if (newName) {
                  setUser(newName); // 이름 업데이트
                }
              }} 
              className="bg-blue-500 text-white p-2 rounded mt-2"
            >
              이름 변경
            </button>
          </div>
          <div className="mb-4">
            <label className="block text-gray-700">사진 변경</label>
            <input
              type="file"
              accept="image/*"
              onChange={handleProfileImageChange} // 파일 변경 핸들러 호출
              className="mt-2"
            />
          </div>
          <div className="mb-4">
            <label className="block text-gray-700">권한 변경</label>
            <button
              onClick={handleRoleChange} // 권한 변경 핸들러 호출
              className="bg-blue-500 text-white p-2 rounded mt-2"
            >
              권한 변경
            </button>
            </div>
          
          <button onClick={togglesettingPopup} className="mt-4 bg-red-500 text-white p-2 rounded">닫기</button>
        </div>
          )}
    
    {/* 알림 드롭다운 */}
    {isNoticeDropdownOpen && (
  <div className="absolute right-0 top-20 mt-2 bg-white border border-gray-300 shadow-lg rounded z-10 " style={{ width: '250px' }}>
      <ul className="py-2 max-h-60 overflow-y-auto">
        {notificationList.length === 0 ? (
          <li className="px-4 py-2 text-gray-500">알림이 없습니다.</li>
        ) : (
          notificationList.map((notification, index) => (
            <li key={index} className="px-4 py-2 hover:bg-gray-100">
              {notification}
            </li>
          ))
        )}
      </ul>
      {/* 닫기 버튼을 스크롤 박스 밖에 배치 */}
      <button 
        onClick={toggleNoticePopup} 
        className="mt-2 w-full text-red-500 border border-gray-300 p-2 rounded text-center"
      >
        닫기
      </button>
    </div>
  )}
  </div>
  );
}

export default Navbar;