// src/components/Login.js
import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

function Login({ onLogin }) {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [errorMessage, setErrorMessage] = useState('');
  const [isSuccess, setIsSuccess] = useState(false); // 로그인 성공 상태 추가
  const navigate = useNavigate();

  const TEMP_USERNAME = '123'; // 임시 아이디
  const TEMP_PASSWORD = '123';   // 임시 비밀번호

  // 컴포넌트가 마운트될 때 세션 스토리지에서 상태를 확인
  useEffect(() => {
    const storedUsername = sessionStorage.getItem('username');
    if (storedUsername) {
      navigate('/Home'); // 이미 로그인 상태인 경우 홈 페이지로 이동
    } else {
      // 상태 초기화
      setUsername('');
      setPassword('');
      setErrorMessage('');
      setIsSuccess(false);
    }
  }, [navigate]);

  const handleSubmit = (e) => {
    e.preventDefault();

    if (username === TEMP_USERNAME && password === TEMP_PASSWORD) {
      setIsSuccess(true); // 로그인 성공 시 상태 변경
      sessionStorage.setItem('username', username); // 세션 스토리지에 사용자명 저장
      onLogin(); // 로그인 함수 호출

      // 애니메이션이 끝난 후 홈 페이지로 이동
      setTimeout(() => {
        navigate('/Home'); // 홈 페이지로 이동
      }, 2000); // 애니메이션 시간 후 이동
    } else {
      setErrorMessage('사용자명 또는 비밀번호가 틀렸습니다.');
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-cover bg-center" style={{ backgroundImage: 'url(/Login_bg.jpg)' }}>
      <div className={`bg-white p-8 rounded-lg shadow-lg w-96 h-100 transition-all duration-500 ${isSuccess ? 'transform scale-75' : ''}`}>
        {!isSuccess ? (
          <>
            <h2 className="text-2xl font-bold text-center mb-6">로그인</h2>
            {errorMessage && (
              <p className="text-red-500 mb-4 text-center">{errorMessage}</p>
            )}
            <form onSubmit={handleSubmit}>
              <div className="mb-4">
                <label className="block text-gray-700 mb-2" htmlFor="username">사용자명</label>
                <input
                  type="text"
                  id="username"
                  value={username}
                  onChange={(e) => setUsername(e.target.value)}
                  className="w-full border border-gray-300 rounded p-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  required
                />
              </div>
              <div className="mb-4">
                <label className="block text-gray-700 mb-2" htmlFor="password">비밀번호</label>
                <input
                  type="password"
                  id="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                  className="w-full border border-gray-300 rounded p-2 focus:outline-none focus:ring-2 focus:ring-blue-500"
                  required
                />
              </div>
              <button
                type="submit"
                className="w-full bg-blue-500 text-white p-2 rounded hover:bg-blue-600"
              >
                로그인
              </button>
            </form>
          </>
        ) : (
          <div className="flex flex-col items-center justify-center">
            <img
              src="/Login_o.png"
              alt="Logo"
              className="w-24 mb-4 transition-transform duration-2000 transform scale-150 mt-20"
            />
            <p className="text-2xl font-bold mt-10">환영합니다!</p>
          </div>
        )}
      </div>
    </div>
  );
}

export default Login;